@import '../../assets/sass/variables';
.category-list{
    
    @include GridList;

    @media only screen and(max-width: 800px){
        grid-template-columns: repeat(2, minmax(25rem, 1fr));
    }

    @media only screen and(max-width: 400px){
        grid-template-columns: repeat(1, minmax(25rem, 1fr));
    }
    
    
}

