$default-font-size: 1.6rem;

@mixin ProductTitle {
    font-size: 2rem;
    // font-weight: 400;
    // font-family: 'Roboto Mono', monospace;
    color: black;
    letter-spacing: 5px;
}

@mixin ProductPrice {
    font-size: $default-font-size;
    color: #787878;
    margin: 0;
}

@mixin GridList {
    width: 90%;
    margin: 5rem auto;
    max-width: 150rem;
    display: grid;
    grid-template-columns: repeat(3, minmax(25rem, 1fr));
    justify-items: center;
    grid-gap: 3rem;
    justify-items: center;
}